*,::after,::before{margin: 0;padding: 0;box-sizing: border-box}
body{margin: 0;font-family: -apple-system,BlinkMacSystemFont,Roboto,Oxygen,Ubuntu,Cantarell,'Fira Sans','Droid Sans','Helvetica Neue',sans-serif;height: 100vh}
.mx-auto{margin: 0 auto}
.container{max-width: 71.25rem}
main{box-shadow: 1px 0 1rem #e8e8e8,-1px 0 1rem #e8e8e8;min-height: 100vh}
header{padding: 1.25rem 1.5rem;background-color: #1f72aa;color: #fff}